$white: white;
$black: black;
$empty-circle: rgba(0,0,0,0);
$font-vt323: 'VT323';
$font-f77: 'F77';

// Background-Styles
$transparent10: transparent 10px;

@font-face {
    font-family: 'F77';
    src: url('../fonts/F77.ttf');
}

%standart-style {
    color: $white;
    font-family: $font-f77;
    display: inline-block;
    vertical-align: top;
    text-transform: uppercase;
    position: relative;
    cursor: pointer;
}

%dis-pos {
    content: "";
    display: block;
    position: absolute;
    background: $black;
    z-index: -1;
}

.pi-btn {
	font-size: 25px;
	height: auto;
	margin: 10px;
	line-height: 0;
	border-style: solid;
	border-width: 20px;
    @extend %standart-style;
    @extend %user-select;
    @extend %image-render;
    @extend %border-image;
	&:active {
		top: 2px;
	}
	p {
		display: inline-block;
		vertical-align: top;
		position: relative;
		width: auto;
		text-align: center;
		margin: -20px -20px;
		line-height: 20px;
		padding: 10px 20px;
		background: $black;
		background: linear-gradient(135deg, $transparent10, $black 0) top left, linear-gradient(225deg, $transparent10, $black 0) top right, linear-gradient(315deg, $transparent10, $black 0) bottom right, linear-gradient(45deg, $transparent10, $black 0) bottom left;
		background-size: 50% 50%;
		background-repeat: no-repeat;
		background-image: radial-gradient(circle at 0 0, $empty-circle 14px, $black 15px), radial-gradient(circle at 100% 0, $empty-circle 14px, $black 15px), radial-gradient(circle at 100% 100%, $empty-circle 14px, $black 15px), radial-gradient(circle at 0 100%, $empty-circle 14px, $black 15px);
	}
}
.pi-btn-big {
	font-size: 25px;
    width: auto;
	height: auto;
	margin: 10px;
	padding: 10px 10px;
	background: $black;
    @extend %standart-style;
    @extend %user-select;
	&:active {
		top: 2px;
	}
	&::before {
		@extend %dis-pos;
		top: 10px;
		bottom: 10px;
		left: -10px;
		right: -10px;
	}
	&::after {
        @extend %dis-pos;
		top: 4px;
		bottom: 4px;
		left: -6px;
		right: -6px;
	}
}

.container {
	margin: 50px;
	display: block;
	width: 400px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}
/*
NOT AN IMPORTABLE CLASS
CREATED TO BE REUSED IN STANDARD CASES
*/

%border-image {
    -moz-border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
	-webkit-border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
	-o-border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
	border-image: url(https://i.imgur.com/sREM8Yn.png) 20 stretch;
}
/*
NOT AN IMPORTABLE CLASS
CREATED TO BE REUSED IN STANDARD CASES
*/

%image-render {
    image-rendering: optimizeSpeed;
	image-rendering: -moz-crisp-edges; //FireFox
	image-rendering: -o-crisp-edges; // Opera
	image-rendering: -webkit-optimize-contrast; // Webkit
	image-rendering: crisp-edges;
    -ms-interpolation-mode: nearest-neighbor; // IE (non-standard property)
}
/*
NOT AN IMPORTABLE CLASS
CREATED TO BE REUSED IN STANDARD CASES
*/

%user-select {
    -webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}